<template>
  <section id="just">
    <span class="title">{{$t('look.limit')}}</span>
    <el-table :data="authoritiesList"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
      <el-table-column prop="authId"  :label="$t('look.thead[0]')">
      </el-table-column>
      <el-table-column prop="authName" :label="$t('look.thead[1]')">
      </el-table-column>
    </el-table>
    <div class="page" v-show="total >10">
      <el-col :span="24" class="toolbar  ">
        <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange">
        </el-pagination>
      </el-col>
    </div>
  </section>
</template>
   
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear} from '../../common/js/common.js'
export default {
   data(){
     return{
       userForm:{
         startTime:'',
         endTime:'',
         enterpriseName:''
       },
       userLoading:false,
       authoritiesList:[],
       authoritiesData:{
        page:1,
        rowNum:"10",
       },
       total:0,
     }
   },
   methods:{
    // 查询权限信息
    queryAuthorities:function(){
      ajax(this,this.ApiConfig.userManagement.auth.queryAuthorities,this.authoritiesData,(res) => {
        console.log(res)
        this.authoritiesList = res.data.rows;
        this.total = res.data.total;
      },"POST")
    },
     getUsers:function(){

     },
     handleReset:function(){

     },
      handleCurrentChange:function(val){
     this.authoritiesData.page = val;
     this.queryAuthorities();
   },
     tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },

   },
   mounted:function(){
    this.queryAuthorities();
   }
}
</script>

<style scoped lang="postcss">
.title {
    font-size: 18px;
    font-weight: 600;
    color: #333;
    font-family: "Microsoft Yahei";

  }
</style>
<style>
  #just{ 
      .el-table th {
      font-size: 18px;
      color: #000;
      font-weight: normal;
     }
     .el-table--fit{
          margin-top: 12px;
     }
     .el-table th {
      font-size: 12px;
      color: #bf9764;
    }
  }
</style>



